              <p className="text-gray-600 mb-8">‡§ï‡•É‡§™‡§Ø‡§æ ‡§Ö‡§™‡§®‡•Ä ‡§ñ‡•ã‡§ú ‡§Ø‡§æ ‡§´‡§ø‡§≤‡•ç‡§ü‡§∞ ‡§¨‡§¶‡§≤‡§®‡•á ‡§ï‡§æ ‡§™‡•ç‡§∞‡§Ø‡§æ‡§∏ ‡§ï‡§∞‡•á‡§Ç</p>
              <button
